<template>
  <div id="app">
    <ChartConfig :selectItems="selectItems"
                 @setItems="setItems" />
    <div class="flexbox">
      <div class="leftPanel">
        <ChartRadio :selectType="selectType"
                    :selectItems="selectItems"
                    @setTypeByRadio="setType" />
        <ChartTabs :selectType="selectType"
                   :selectItems="selectItems"
                   @setTypeByTabs="setType" />
        <ChartSelect :selectType="selectType"
                     :selectItems="selectItems"
                     @setTypeBySelect="setType" />
      </div>
      <ChartView class="rightPanel"
                 :selectItems="selectItems"
                 :selectCount="selectCount" />
    </div>
  </div>
</template>

<script>
// eslint-disable-next-line
import ChartConfig from '@/components/ChartConfig';
// eslint-disable-next-line
import ChartRadio from '@/components/ChartRadio';
// eslint-disable-next-line
import ChartTabs from '@/components/ChartTabs';
// eslint-disable-next-line
import ChartSelect from '@/components/ChartSelect';
// eslint-disable-next-line
import ChartView from '@/components/ChartView';

export default {
  name: 'app',
  components: {
    ChartConfig, ChartRadio, ChartTabs, ChartSelect, ChartView,
  },
  data() {
    return {
      selectItems: [1, 2, 3, 4],
      selectCount: [0, 0, 0, 0],
      selectType: 1,
    };
  },
  beforeMount() {
    this.selectCount[0] += 1;
  },
  methods: {
    setItems(value) {
      this.selectCount = [0, 0, 0, 0];
      this.selectItems = value;
      this.selectCount[this.selectItems[0] - 1] += 1;
      // eslint-disable-next-line
      this.selectType = this.selectItems[0];
      this.$bus.$emit('newItems', this.selectItems);
      this.$bus.$emit('newCount', this.selectCount);
    },
    setType(value) {
      this.selectType = value;
      this.selectCount[value - 1] += 1;
      this.$bus.$emit('newCount', this.selectCount);
    },
  },
};
</script>

<style lang="scss">
@import './assets/style.scss';
#app {
  width: 1024px;
  border: $border;
  border-radius: 4px;
  margin: 0 auto;
  font-family: Arial;
