<script>
export default {
  name: 'ChartSelect',
  props: ['selectType', 'selectItems'],
  data() {
    return {
      selectOn: false,
    };
  },
  computed: {
    selectTypeChild() { return this.selectType; },
    setTypeBySelect: {
      get() {
        return this.selectType;
      },
      set(value) {
        this.$emit('setTypeBySelect', value);
      },
    },
  },
  methods: {
    setTypeByInput(e) {
      console.log(e.target);

      const Reg = /[1-4]{1}/;
      if (Reg.test(e.target.value)) {
        this.$emit('setTypeBySelect', e.target.value);
      } else {
        // eslint-disable-next-line
        alert('Error pleace type 1-4');
        this.$emit('setTypeBySelect', 1);
      }
    },
  },
};
</script>
