<script>
export default {
  name: 'ChartRadio',
  props: ['selectType', 'selectItems'],
  data() {
    return {
    };
  },
  computed: {
    setTypeByRadio: {
      get() {
        return this.selectType;
      },
      set(value) {
        this.$emit('setTypeByRadio', value);
      },
    },
  },
};
</script>
