<script>
export default {
  name: 'ChartTabs',
  props: ['selectType', 'selectItems'],
  data() {
    return {
    };
  },
  computed: {
    setTypeByTabs: {
      get() {
        return this.selectType;
      },
      set(value) {
        this.$emit('setTypeByTabs', value);
      },
    },
  },
};
</script>
