<script>
export default {
  props: ['selectItems', 'selectCount'],
  data() {
    return {
      selectItemsChild: this.selectItems,
      selectCountChild: this.selectCount,
      chartData: {
        columns: ['type', 'count'],
        rows: [
          {
            type: 1, count: 0,
          },
          {
            type: 2, count: 0,
          },
          {
            type: 3, count: 0,
          },
          {
            type: 4, count: 0,
          },
        ],
      },
    };
  },
  created() {
    // [註冊監聽事件]
    this.$bus.$on('newItems', (event) => {
      this.selectItemsChild = event;
      this.newChart();
    });
    this.$bus.$on('newCount', (event) => {
      this.selectCountChild = event;
      this.newChart();
    });
  },
  mounted() {
    let tempObj = {};
    const tempArr = [];
    for (let i = 0; i < this.selectItemsChild.length; i += 1) {
      tempObj = {};
      tempObj.type = this.selectItemsChild[i];
      tempObj.count = this.selectCountChild[this.selectItemsChild[i] - 1];
      tempArr.push(tempObj);
    }
    this.chartData.rows = tempArr;
  },
  beforeDestroy() {
    // [銷毀監聽事件]
    this.$bus.$off('newItems');
    this.$bus.$off('newCount');
  },
  methods: {
    newChart() {
      let tempObj = {};
      const tempArr = [];
      for (let i = 0; i < this.selectItemsChild.length; i += 1) {
        tempObj = {};
        tempObj.type = this.selectItemsChild[i];
        tempObj.count = this.selectCountChild[this.selectItemsChild[i] - 1];
        tempArr.push(tempObj);
      }
      this.chartData.rows = tempArr;
    },
  },
};
</script>
<template src="./ChartView.html"></template>
<style lang="scss" src="./ChartView.scss" scoped></style>
