<script>
export default {
  name: 'ChartConfig',
  props: ['selectItems'],
  data() {
    return {
      selectItemsStr: '',
      error: false,
    };
  },
  mounted() {
    this.selectItemsStr = this.selectItems.join(',');
  },
  methods: {
    setItems() {
      // eslint-disable-next-line
      const Reg = /^([1-4]\,){0,3}([1-4]){1}$/;
      if (Reg.test(this.selectItemsStr)) {
        const isRepeat = this.checkArray(this.selectItemsStr.split(','));
        if (!isRepeat) {
          this.$emit('setItems', this.selectItemsStr.split(','));
          this.error = false;
          return;
        }
        this.error = true;
      } else {
        this.error = true;
      }
    },
    checkArray(arr) {
      const map = {};
      for (let i = 0, size = arr.length; i < size; i += 1) {
        if (map[arr[i]]) {
          return true;
        }
        map[arr[i]] = true;
      }
      return false;
    },
  },
};
</script>
<template src="./ChartConfig.html"></template>
<style lang="scss" src="./ChartConfig.scss"></style>
